<template>
  <div ref="content">
    <div v-for="img in images" :key="img.id" class="pdfcon">
      <img :src="img.imageUrl" class="imageforpdf" />
    </div>
    <v-card-actions>
      <v-btn class="mx-auto success" large tile @click="downloadPdf"
        >Download PDF</v-btn
      >
    </v-card-actions>
  </div>
</template>

<script>
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  methods: {
    downloadPdf() {
      const doc = new jsPDF();
      /** WITH CSS */
      var canvasElement = document.createElement("canvas");
      html2canvas(this.$refs.content, { canvas: canvasElement }).then(
        (canvas) => {
          const img = canvas.toDataURL("image/jpeg", 0.8);
          doc.addImage(img, "JPEG", 20, 20);
          doc.save("sample.pdf");
        }
      );
    },
  },
  computed: {
    images: {
      get() {
        return this.$store.state.images;
      },
      set(value) {
        this.$store.commit("updateList", value);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.pdfcon {
  width: 100vw;
}
.imageforpdf {
  height: 100%;
  width: 100%;
}
</style>